.tb_c_m_post_container {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
}
.tb_c_m_post_slider {
    width: 100%;
}

.tb_c_m_post_wrapper {
    width: 100%;
}

.tb_c_m_post_in {
    display: block;
    position: relative;
    cursor: pointer;
    overflow: hidden;
    box-shadow: 0 0 1.5px rgba(0, 0, 0, 0.20);
    background-color: var(--tb-bg-color);
    border-radius: var(--tb-border-radius);
}

.tb_c_m_post_media_wrapp{
    position: relative;
    display: block;
    width: 100%;
}
.tb_c_m_image{
    height: 100% !important;
    width: 100% !important;
    position: absolute;
    left: 0;
    top: 0;
    margin: 0;
    z-index: 1;
    overflow: hidden;
    display: block;
    transition: transform 3.525s .01s ease-out, opacity 3.525s ease-out;
}

.tb_c_m_video_wrap_,
.tb_c_m_image_wrap_{
    position: relative;
    padding-bottom: calc(var(--tb-theme-height) / 2);
}

.tb_c_m_row-1 .tb_c_m_video_wrap_,
.tb_c_m_row-1 .tb_c_m_video_wrap_{
    padding-bottom: var(--tb-theme-height);
}

// .tb_wall_container .tb_c_m_image_wrap_, 
// .tb_wall_container .tb_c_m_video_wrap_{
//     padding-bottom: calc(var(--tb-theme-height) - var(--tb-padding));
// }
.tb_c_m_image, .tb_c_m_video{
    height: 100% !important;
    width: 100% !important;
    position: absolute;
    left: 0;
    top: 0;
    margin: 0;
    z-index: 1;
    overflow: hidden;
    display: block;
    object-fit: cover;
    object-position: center;
    background-color: #000;
}

.tb_c_m_video video{
    height: 100%;
    width: 100%;
    object-fit: cover;
    object-position: center;
}

.tb_c_m_post_media_wrapp .fb_iframe_widget_fluid{
    width: 100%;
    display: flex;
    align-items: center;
}

.tb_c_m_video video::-webkit-media-controls-panel {
    background-image: linear-gradient(transparent, transparent) !important;
}


.tb_c_m_post_details,
.tb_c_m_post_overlay {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 10px;
    transition: opacity .3s ease-in-out, visibility .3s ease-in-out;
    opacity: 0;
    visibility: hidden;
    z-index: 2;
}

.tb_c_m_post_details {
    z-index: 4;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.tb_c_m_post_overlay-light {
    background-color: rgba(255, 255, 255, .3);
    background-image: linear-gradient(135deg, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, .4) 100%);
}

.tb_c_m_post_overlay-dark {
    background-color: rgba(0, 0, 0, .2);
    background-image: linear-gradient(135deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, .4) 100%);
}

@media only screen and (min-width: 991px)  {
    .tb_c_m_post_in:hover .tb_c_m_post_details,
    .tb_c_m_post_in:hover .tb_c_m_post_overlay {
        opacity: 1;
        visibility: visible;
    }

    .tb_c_m_post_in:hover .tb_c_m_post_overlay.tb_c_m_color_overlay {
        opacity: .8;
    }

    .tb_rt_post_animate:hover .tb_rt_image {
        -webkit-transform: scale(1.07);
        transform: scale(1.07);
    }


    .tb_c_m_post_ani:hover .tb_c_m_post_details{
        transform: translateY(10px);
        animation: TbSlideUpAnimation_ .65s ease forwards;
    }

    @-webkit-keyframes TbSlideUpAnimation_ {
        100% {
            transform: translateY(0);
        }

        100% {
            transform: translateY(0);
        }
    }

    @keyframes TbSlideUpAnimation_ {
        0% {
            transform: translateY(10px);
        }

        100% {
            transform: translateY(0);
        }
    }

    .tb_c_m_post_ani:hover .tb__media_ico_ .tb__icon,
    .tb_c_m_post_ani:hover .tb__media_ico_ .tb__icon{
        opacity: 0;
        transform: scaleX(.4) scaleY(.4) translateZ(0);
    }
}

.tb_c_m_col_wrap{
    display: block;
    width: 100%;
}

.tb_c_m_contant_wrapper {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    margin: 0 auto;
    height: 100%;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    padding: 15px;
}

.tb_c_m_social_ {
    display: flex;
    text-align: center;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin: 15px 0;
}

.tb_c_m_author_profile {
    height: 44px;
    width: 44px;
    border-radius: 50%;
    margin-right: 10px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    display: flex;
    flex-shrink: 0;
    background-color: rgba(0, 0, 0, .2);
    overflow: hidden;
}

.tb_c_m_author_profile__ {
    height: 100% !important;
    width: 100% !important;
    margin: 0;
    object-fit: cover;
    object-position: center;
}

.tb_c_m_authorname {
    font-size: 14px;
    font-weight:  var(--tb-author-font-weight);
    margin-bottom: 4px;
    white-space: nowrap;
    display: block;
    text-align: left;
    overflow: hidden;
    text-overflow: ellipsis;
    line-height: normal;
}

.tb_c_m_authorname {
    font-family: var(--tb-author-font);
    color: #fff;
    font-style:  var(--tb-author-font-style);
}

.tb_c_m_username,
.tb_c_m_time {
    font-family: var(--tb-font);
    color: #fff;
}

.tb_c_m_seprator {
    background-color: #fff;
}

.tb_c_m_content {
    font-family: var(--tb-font);
    color: #fff;
    font-size: var(--tb-font-size);
    line-height: var(--tb-line-height);
    font-weight: var(--tb-font-weight);
    font-style: var(--tb-font-style);
}

.tb_c_m_author {
    display: flex;
    align-items: center;
    flex-grow: 1;
    width: calc(100% - 30px);
}

.tb_c_m_author_wrapper {
    display: flex;
    align-items: center;
    position: absolute;
    width: 100%;
    bottom: 0;
    left: 0;
    padding: 10px;
}

.tb_c_m_author_wrapper.tb_c_m_author_wrapper_top {
    top: 0;
    bottom: auto;
}

.tb_c_m_author_info {
    overflow: hidden;
    display: flex;
    flex-shrink: 0;
    flex-direction: column;
    width: calc(100% - 54px);
    padding-right: 10px;
}

.tb_c_m_post_info {
    display: flex;
    align-items: center;
}

.tb_c_m_seprator {
    height: 4px;
    width: 4px;
    min-width: 4px;
    border-radius: 50%;
    margin: 0 6px;
    opacity: 0.8;
    display: flex;
    flex-shrink: 1;
}

.tb_c_m_username,
.tb_c_m_time {
    font-size: 10px;
    font-weight: 500;
    opacity: 0.8;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    line-height: normal;
}

.tb_c_m_username {
    max-width: 65%;
}

.tb_c_m_time {
    max-width: 35%;
    min-width: 65px;
}

.tb_c_m_social__ico {
    font-size: 24px;
    color: var(--tb-icon-color);
}

.tb_c_m_social_bottom_ {
    position: absolute;
    z-index: 3;
    bottom: 15px;
    left: 0;
    right: 0;
    text-align: center;
}

.tb_c_m_content {
    text-align: center;
    white-space: pre-line;
    overflow: hidden;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 4;
    text-overflow: ellipsis;
    word-break: break-word;
    white-space: pre-line;
    width: 100%;
}

.tb_c_m_content .tb_text_links_ {
    color: inherit !important;
    text-decoration: underline !important;
    display: inline;
}

.tb_c_m_post_in:hover .tb_share_wrapper {
    opacity: 1;
    visibility: visible;
}

.tb_c_m_video_ico,
.tb_c_m_audio_ico,
.tb_c_m_video_ply_ico {
    position: absolute;
    right: 12px;
    top: 12px;
    z-index: 2;
    color: #fff;
    filter: drop-shadow(2px 2px 2px rgba(0, 0, 0, .4));
}

.tb_c_m_video_ico {
    font-size: 12px;
}

.tb_c_m_video_ply_ico {
    font-size: 18px;
    z-index: 1;
}

.tb_c_m_audio_ico {
    font-size: 16px;
}

.tb_c_m_multi_images_ico {
    position: absolute;
    right: 12px;
    top: 12px;
    color: #fff;
    font-size: 17px;
    z-index: 2;
    filter: drop-shadow(2px 2px 2px rgba(0, 0, 0, .4));
}

.tb_c_m_multi_images_ico_bottom {
    position: absolute;
    right: 12px;
    bottom: 12px;
    color: #fff;
    font-size: 17px;
    filter: drop-shadow(2px 2px 2px rgba(0, 0, 0, .4));
}

.tb_c_m_youtube_ico {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    margin: 0 auto;
    text-align: center;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 34px;
}

.tb_c_m_share_container_te.tb_share_wrapper {
    left: 18px;
    top: 65px;
}

.tb_c_m_arrow {
    position: absolute;
    top: calc(50% - 20px);
    background-color: #fff;
    border-radius: 3px;
    height: 40px;
    width: 40px;
    display: flex;
    text-align: center;
    align-items: center;
    justify-content: center;
    color: #000;
    font-size: 16px;
    opacity: 0.75;
    z-index: 9;
    cursor: pointer;
    -webkit-transform: translateY(0) !important;
    transform: translateY(0) !important;
}

.tb_c_m_arrow:hover {
    opacity: 1;
}

.tb_c_m_arrow_left__ {
    left: 5px;
    right: auto;
    box-shadow: 3px 0px 3px rgba(0, 0, 0, 0.15);
}

.tb_c_m_arrow_right__ {
    right: 5px;
    left: auto;
    box-shadow: -3px 0px 3px rgba(0, 0, 0, 0.15);
}

.tb_a_spinner.tb_c_m_arrow_right__:before {
    content: "";
    position: absolute;
    top: 50%;
    border-radius: 50%;
    width: 18px;
    height: 18px;
    margin-top: -9px;
    border: 1.5px solid #000;
    border-right: 1.5px solid transparent;
    left: auto;
    right: 12px;
    display: flex;
    -webkit-animation: tb_btn_animation-spinner 0.5s linear infinite;
    animation: tb_btn_animation-spinner 0.5s linear infinite;
}

@-webkit-keyframes tb_btn_animation-spinner {
    to {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}

@keyframes tb_btn_animation-spinner {
    to {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}

.tb_c_m_image_wrap_in {
    position: absolute;
    height: 100%;
    margin: auto;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    width: 100%;
}


.tb_c_m_image {
    width: 100% !important;
    height: 100% !important;
    display: block;
    object-fit: cover;
    object-position: center;
}
.tb_c_m_image_wrap_ img{
    max-width: 100%;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
    margin: 0 !important;
}

.tb_c_m_image, .tb_c_m_video{
    height: 100% !important;
    width: 100% !important;
    position: absolute;
    left: 0;
    top: 0;
    margin: 0;
    z-index: 1;
    overflow: hidden;
    display: block;
    object-fit: cover;
    object-position: center;
    background-color: #000;
}

.tb_c_m_video video{
    height: 100%;
    width: 100%;
    object-fit: cover;
    object-position: center;
}

.tb_c_m_video iframe{
    pointer-events: none;
}

.tb_c_m_media_iframe_video span,
.tb_c_m_media_iframe_video iframe {
    height: 100% !important;
    display: block !important;
    width: 100% !important;
    border: none;
}

.tb_c_m_media_iframe_video video {
    border: none;
    width: 100%;
    height: 100%;
    object-position: center;
    object-fit: cover;
}

.tb_c_m_post_wrapper2x .tb_c_m_author_profile {
    height: 30px;
    width: 30px;
    margin-right: 5px;
}

.tb_c_m_post_wrapper2x .tb_c_m_authorname {
    font-size: 10px;
    margin-bottom: 0px;
}

.tb_c_m_post_wrapper2x .tb_c_m_post_info {
    display: none;
}

.tb_c_m_post_wrapper2x .tb_c_m_contant_wrapper {
    padding: 7px;
}

.tb_c_m_post_wrapper2x .tb_c_m_content {
    -webkit-line-clamp: 2;
}

.tb_c_m_post_wrapper2x .tb_c_m_author_info {
    padding-right: 10px;
    width: calc(100% - 30px);
}

.tb_c_m_post_wrapper2x .tb_c_m_author_wrapper {
    padding: 5px;
}

.tb_c_m_post_in:hover .tb_c_m_like.tb_like_ico{
    bottom: 70px;
}

.tb_c_m_like.tb_like_ico{
    bottom: 6px;
    right: 6px;
    background-color: transparent;
    box-shadow: none;
    transition: all .3s ease-out;
}

.tb_c_m_like.tb_like_ico .tb__icon{
    color: #fff;
    font-size: 25px;
}

.tb_c_m_like.tb_animate .tb__icon{
    color: red;
}